<template>
  <div class="app">
    <Menu :width="menuWidth"
          @clickName="handleSelectName"
          @selectDoc="handleSelectDoc"
          @selectAuth="handleSelectAuth"
          @selectApi="handleSelectApi" />
    <div :style="{marginLeft: menuWidth + 'px'}"
         class="api-container">
      <I18n class="locals" />
      <Main v-if="isMain && baseInfo.read_me"
            :desc="baseInfo.read_me" />
      <AuthInfo v-else-if="auth !== null"
                :auth="auth" />
      <Doc v-else-if="doc !== null"
           :doc="doc" />
      <Api v-else-if="api !== null"
           :api="api" />
    </div>
  </div>
</template>

<script>
import Main from './components/Main/index.vue';
import AuthInfo from './components/Main/Auth.vue';
import Menu from './components/Menu/index.vue';
import Api from './components/API/index.vue';
import Doc from './components/Doc/index.vue';
import I18n from './components/i18n.vue';

export default {
  components: {
    Main,
    AuthInfo,
    Menu,
    Api,
    Doc,
    I18n,
  },
  mounted() {
    this.$store.dispatch('getApiDocs');
    const isMain = localStorage.getItem('app.isMain');
    this.isMain = isMain === 'true' || isMain === null;
  },
  computed: {
    baseInfo() {
      return this.$store.state.baseInfo;
    },
  },
  data() {
    return {
      api: {},
      doc: {},
      auth: null,
      isMain: true,
      menuWidth: 250,
    };
  },
  methods: {
    handleSelectName() {
      this.isMain = true;
      localStorage.setItem('app.isMain', true);
    },
    handleSelectAuth(auth) {
      this.auth = auth;
      this.api = null;
      this.hideMain();
    },
    handleSelectDoc(doc) {
      this.doc = doc;
      this.auth = null;
      this.api = null;
      this.hideMain();
    },
    handleSelectApi(api) {
      this.api = api;
      this.doc = null;
      this.hideMain();
    },
    hideMain() {
      this.isMain = false;
      localStorage.setItem('app.isMain', false);
    },
  },
};
</script>

<style>
html,
body,
.app,
html,
body {
  margin: 0;
  padding: 0;
  height: 100%;
}
* {
  color: inherit #202d40;
}
</style>
<style lang="scss" scoped>
.app {
  display: flex;

  .api-container {
    flex: 1;
    padding: 20px;
  }
}
.locals {
  position: fixed;
  top: 10px;
  right: 10px;
}
</style>
