import Vue from 'vue';
import Vuex from 'vuex';
import { getApiDocs, getDocs, getModel } from '../api/docs';

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    baseInfo: {},
    docs: {},
    models: {},
  },
  mutations: {
    SET_BASE_INFO(state, info) {
      state.baseInfo = info;
      document.title = `${info.project_name} Panda api`;
    },
    ADD_DOCS(state, { filename, doc }) {
      state.docs[filename] = doc;
    },
    ADD_MODEL(state, { filename, model }) {
      state.models[filename] = model;
    },
  },
  actions: {
    async getApiDocs({ commit }) {
      const res = await getApiDocs();
      const baseInfo = {
        ...res.data,
        api_docs: res.data.api_docs.sort((a, b) => a.order - b.order),
      };
      const finderSettings = baseInfo.api_docs.find(d => d.filename === '_settings.json5');
      if (finderSettings) {
        const { data: settings } = await getDocs(finderSettings.filename);
        baseInfo.api_docs = baseInfo.api_docs.filter(d => d.filename !== '_settings.json5');
        baseInfo.settings = settings;
      }
      commit('SET_BASE_INFO', baseInfo);
    },
    getDocs({ state, commit }, { filename }) {
      if (filename in state.docs) {
        return Promise.resolve(state.docs[filename]);
      }
      return getDocs(filename).then((res) => {
        commit('ADD_DOCS', { filename, doc: res.data });
        return res.data;
      });
    },
    getModel({ state, commit }, { filename }) {
      if (filename in state.models) {
        return Promise.resolve(state.models[filename]);
      }
      return getModel(filename).then((res) => {
        commit('ADD_MODEL', { filename, doc: res.data });
        return res.data;
      });
    },
  },
});
