<template>
  <div class="type">
    <div class="title">
      {{title}}
    </div>
    <div class="body"
         v-if="innerBody.length > 0">
      <div class="field"
           v-for="field in innerBody"
           :key="field.prop">
        <Field :field="field" />
      </div>
    </div>
    <div class="none"
         v-else>{{$t('none_body')}}</div>
  </div>
</template>

<script>
import Field from './ComplexType/Field.vue';

export default {
  name: 'simple-type',
  props: {
    title: String,
    body: Object,
  },
  computed: {
    innerBody() {
      const body = [];
      if (this.body) {
        Object.keys(this.body).forEach((prop) => {
          if (!prop.startsWith('$')) {
            const value = this.body[prop];
            const isArray = Array.isArray(value);
            let newItem = {
              prop,
            };
            if (isArray) {
              newItem.type = 'array';
              // eslint-disable-next-line prefer-destructuring
              const originArrayMember = value[0];
              if ('type' in originArrayMember && typeof originArrayMember.type === 'string') {
                newItem.item = {
                  type: originArrayMember.type,
                  name: originArrayMember.name,
                };
              } else {
                const arrayMember = {};
                Object.keys(originArrayMember).forEach((key) => {
                  if (!key.startsWith('$')) {
                    arrayMember[key] = originArrayMember[key];
                  }
                });
                newItem.fields = arrayMember;
              }
            } else {
              newItem = {
                ...newItem,
                ...value,
              };
            }
            body.push(newItem);
          }
        });
      }
      return body;
    },
  },
  components: {
    Field,
  },
};
</script>

<style lang="scss" scoped>
.type {
  .title {
    padding: 10px 0;
    font-weight: 600;
    font-size: 1.2rem;
    border-bottom: 2px solid #ddd;
  }

  .field {
    padding: 10px 0;
    border-bottom: 1px solid #eee;
  }

  .none {
    margin: 10px 0;
  }
}
</style>
