<template>
  <div class="menu"
       :style="{width: width+'px'}">
    <div class="title"
         @click="handleClickName">{{baseInfo.project_name}}</div>
    <div class="docs__menu"
         v-loading="loading">
      <div class="doc__item"
           v-for="doc in baseInfo.api_docs"
           :key="doc.filename">
        <div v-if="doc.filename === authFileName"
             class="doc__name"
             :class="{'active':clickedAuth}"
             @click="showAuth()">{{doc.name}}</div>
        <div v-else
             class="doc__name"
             :class="{'active': currentFilename === doc.filename && currentApiIndex === -1}"
             @click="showApi(doc.filename)">{{doc.name}}</div>
        <div class="doc__list"
             v-if="currentFilename === doc.filename">
          <div class="line"></div>
          <div class="line--current"
               v-if="currentApiIndex >= 0"
               :style="{top: currentApiIndex * 40 + 'px'}"></div>
          <div class="doc"
               v-for="(api,index) in currentDoc.apis"
               @click="getApi(api,index)"
               :class="{'doc--active': currentApi === api.name}"
               :key="api.name">
            {{api.name}}
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    width: {
      type: Number,
      default: 250,
    },
  },
  computed: {
    baseInfo() {
      return this.$store.state.baseInfo;
    },
  },
  data() {
    return {
      authFileName: '_auth.json5',
      loading: false,
      currentFilename: '',
      currentDoc: {},
      currentApi: '',
      currentApiIndex: -1,
      clickedAuth: false,
    };
  },
  mounted() {
    const isMain = localStorage.getItem('app.isMain');
    if (isMain === 'false') {
      const currentFilename = localStorage.getItem('lastFileName');
      if (currentFilename) {
        if (currentFilename === this.authFileName) {
          this.showAuth();
        } else {
          const currentApiIndex = localStorage.getItem('currentApiIndex');
          if (currentApiIndex) {
            const index = Number(currentApiIndex);
            if (index > -1) {
              this.showApi(currentFilename).then((doc) => {
                const api = doc.apis[index];
                this.getApi(api, index);
              });
            } else {
              this.showApi(currentFilename);
            }
          }
        }
      }
    }
  },
  methods: {
    handleClickName() {
      this.$emit('clickName');
      this.clearClick();
    },
    clearClick() {
      this.currentApi = '';
      this.currentApiIndex = -1;
      this.currentDoc = {};
      this.currentFilename = '';
    },
    showAuth() {
      this.clickedAuth = true;
      this.clearClick();
      this.$store.dispatch('getDocs', { filename: this.authFileName })
        .then((auth) => {
          localStorage.setItem('lastFileName', this.authFileName);
          this.$emit('selectAuth', auth);
        }).finally(() => {
          this.loading = false;
        });
    },
    showApi(filename) {
      this.clickedAuth = false;
      // const lastFilename = localStorage.getItem('lastFileName');
      this.loading = true;
      return this.$store.dispatch('getDocs', { filename })
        .then((doc) => {
          this.currentDoc = doc;
          this.currentFilename = filename;
          this.currentApiIndex = -1;
          this.currentApi = '';
          this.$emit('selectDoc', doc);
          localStorage.setItem('lastFileName', filename);
          localStorage.setItem('currentApiIndex', this.currentApiIndex);
          return doc;
          // if (doc.apis.length > 0) {
          //   const lastApiIndex = Number(localStorage.getItem('currentApiIndex')) || 0;
          //   const defaultIndex = lastFilename === filename ? lastApiIndex : 0;
          //   this.getApi(doc.apis[defaultIndex], defaultIndex);
          // }
        }).finally(() => {
          this.loading = false;
        });
    },
    getApi(api, index) {
      this.currentApi = api.name;
      this.currentApiIndex = index;
      this.$emit('selectApi', api);
      localStorage.setItem('currentApiIndex', index);
    },
  },
};
</script>
<style lang="scss" scoped>
.menu {
  position: fixed;
  box-sizing: border-box;
  height: 100vh;
  z-index: 10000;
  box-shadow: 2px 0 5px #ddd;
  background-color: white;
  overflow: auto;
  cursor: pointer;

  .title {
    padding: 20px 10px;
    font-size: 1.2rem;
    font-weight: 600;
  }

  .doc__list {
    position: relative;

    .line {
      position: absolute;
      height: 100%;
      width: 2px;
      left: 10px;
      background-color: #ddd;

      &--current {
        position: absolute;
        height: 40px;
        width: 2px;
        left: 10px;
        background-color: #2372fa;
        transition: 0.2s;
      }
    }
  }

  .active {
    color: #2372fa;
    background-color: rgba(35, 114, 250, 0.1);
  }

  .doc__name,
  .doc {
    padding: 10px;
    transition: 0.2s;
    text-overflow: ellipsis;
    overflow: hidden;
    white-space: nowrap;
    &:hover,
    &--active {
      color: #2372fa;
      background-color: rgba(35, 114, 250, 0.1);
    }
  }

  .doc {
    padding: 10px 0 10px 30px;
    font-size: 0.9rem;
  }
}
</style>
