<template>
  <div class="field__item">
    <div class="row">
      <span class="prop">{{field.prop}}</span>
      <span class="type">{{parentType}}</span>
      <span class="required"
            v-if="!getRequired">{{$t('optional')}}</span>
      <span class="name">{{field.desc || field.name || $t('none')}}</span>
    </div>
    <complex-type :field="field"
                  v-if="isComplexType" />
  </div>
</template>

<script>
import ComplexType from './index.vue';
import { getParentType } from '../../../utils';

export default {
  name: 'field',
  components: {
    ComplexType,
  },
  props: {
    field: Object,
  },
  computed: {
    isComplexType() {
      return ['array', 'enum', 'object'].includes(this.field.type) || 'enum' in this.field;
    },
    getRequired() {
      return 'required' in this.field ? this.field.required : true;
    },
    parentType() {
      const field = this.field.type || 'string';
      const parent = getParentType(field);
      return parent ? `${field} : ${parent}` : field;
    },
  },
};
</script>

<style lang="scss" scoped>
.row {
  margin: 10px 0;

  .type {
    color: #aaa;
    margin-left: 10px;
  }

  .required {
    border-radius: 2px 4px;
    border: 1px solid #ccc;
    margin-left: 10px;
    color: #444;
    font-size: 0.9rem;
    padding: 2px;
  }

  .name {
    margin-left: 10px;
  }
}
</style>
