<template>
  <div v-if="type !== 'object'">
    <div class="title">{{$t('array_member')}} ({{arrayType}})</div>
    <div class="name">{{type.name}}</div>
  </div>
  <ObjectType v-else
              :title="$t('array_member') + ' (object)'"
              :fields="fields">
  </ObjectType>
</template>

<script>
import ObjectType from './ObjectType.vue';
import { getParentType } from '../../../utils';

export default {
  components: {
    ObjectType,
  },
  props: {
    type: [String, Object],
    fields: Object,
  },
  computed: {
    arrayType() {
      const parent = getParentType(this.type.type);
      return parent ? `${this.type.type} : ${parent}` : this.type.type;
    },
  },
};
</script>

<style lang="scss" scoped>
.title {
  padding: 10px;
  background-color: #eee;
  border-bottom: 1px solid #ddd;
}

.name {
  padding: 10px;
  font-size: 0.8rem;
}
</style>
